.search-form {
  z-index: 100;
  background: radial-gradient(circle, #090979 52%, #00d4ff 100%);
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  position: fixed;
  top: 0;
  box-shadow: 0 3px 5px #00000080;
}

input[type="text"] {
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  width: 250px;
  height: 50px;
  font-size: 24px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #00d4ff;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  width: 80px;
  height: 50px;
  font-size: 16px;
  font-weight: 700;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

button[type="submit"]:hover {
  color: #1515c9;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 650px) {
  input[type="text"] {
    width: 350px;
  }
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 10px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 20px;
  display: flex;
  position: absolute;
  top: 120px;
  left: 0;
  right: 0;
}

@media screen and (min-width: 650px) {
  .gallery {
    width: 760px;
  }
}

@media screen and (min-width: 1310px) {
  .gallery {
    width: 1440px;
  }
}

.photo-card {
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;
  height: 360px;
  margin: 0;
  display: flex;
  overflow: hidden;
  box-shadow: 0 3px 8px #00000080;
}

.photo {
  object-fit: cover;
  width: 100%;
  height: 290px;
  display: block;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 70px;
  padding: 0 5px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0 5px;
  display: flex;
}

@media screen and (min-width: 650px) {
  .photo-card {
    flex-basis: calc(50% - 20px);
    width: 100%;
  }
}

@media screen and (min-width: 1310px) {
  .photo-card {
    flex-basis: calc(25% - 20px);
  }
}

